import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
	// mode: 'history',
	//base: '/addons/we7_wmall/',
	scrollBehavior (to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition;
		} else {
			if (from.meta.keepAlive) {
				from.meta.savedPosition = document.body.scrollTop;
			}
			return { x: 0, y: to.meta.savedPosition || 0};
		}
	},
	routes: [
		{
			path: '/',
			redirect: '/pages/order/takeout',
		},
		{
			path: '/pages/home/index',
			name: 'homeIndex',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/home/index'], resolve),
		},
		{
			path: '/pages/order/takeout', //
			name: 'takeoutList',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/order/takeout'], resolve),
		},
		{
			path: '/pages/order/detail',
			name: 'takeoutDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/order/detail'], resolve),
		},
		{
			path: '/pages/order/op',
			name: 'takeoutOp',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/order/op'], resolve),
		},
		{
			path: '/pages/finance/getcashList',
			name: 'financeGetcashList',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/getcashList'], resolve),
		},
		{
			path: '/pages/finance/getcashDetail',
			name: 'financeGetcashDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/getcashDetail'], resolve),
		},
		{
			path: '/pages/finance/current',
				name: 'financeCurrent',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/current'], resolve),
		},
		{
			path: '/pages/finance/currentDetail',
			name: 'financeCurrentDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/currentDetail'], resolve),
		},
		{
			path: '/pages/finance/index',
			name: 'financeIndex',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/index'], resolve),
		},
		{
			path: '/pages/finance/getcash',
			name: 'financeGetcash',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/getcash'], resolve),
		},
		{
			path: '/pages/finance/account',
			name: 'financeAccount',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/finance/account'], resolve),
		},
		{
			path: '/pages/member/mine',
			name: 'memberMine',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/member/mine'], resolve),
		},
		{
			path: '/pages/member/setting',
			name: 'memberSetting',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/member/setting'], resolve),
		},
		{
			path: '/pages/member/profilePassword',
			name: 'memberProfilePassword',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/member/profilePassword'], resolve),
		},
		{
			path: '/pages/paotui/index',
			name: 'paotuiList',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/paotui/index'], resolve),
		},
		{
			path: '/pages/paotui/detail',
			name: 'paotuiDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/paotui/detail'], resolve),
		},
		{
			path: '/pages/paotui/op',
			name: 'paotuiOp',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/paotui/op'], resolve),
		},
		{
			path: '/pages/member/phonic',
			name: 'memberPhonic',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/member/phonic'], resolve),
		},
		{
			path: '/pages/statcenter/index',
			name: 'statcenterIndex',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/index'], resolve),
		},
		{
			path: '/pages/statcenter/takeout',
			name: 'statcenterTakeout',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/takeout'], resolve),
		},
		{
			path: '/pages/statcenter/errander',
			name: 'statcenterErrander',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/errander'], resolve),
		},
		{
			path: '/pages/statcenter/detail',
			name: 'statcenterDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/detail'], resolve),
		},
		{
			path: '/pages/statcenter/rank',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/rank'], resolve),
		},
		{
			path: '/pages/statcenter/erranderRank',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/erranderRank'], resolve),
		},
		{
			path: '/pages/auth/login',
			name: 'authLogin',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/auth/login'], resolve),
		},
		{
			path: '/pages/auth/forget',
			name: 'authForget',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/auth/forget'], resolve),
		},
		{
			path: '/pages/comment/list',
			name: 'commentList',
				meta: {
				title: ''
			},
			component: resolve => require(['@/pages/comment/list'], resolve),
		},
		{
			path: '/pages/home/news/index',
			name: 'newsIndex',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/home/news/index'], resolve),
		},
		{
			path: '/pages/home/news/detail',
			name: 'newsDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/home/news/detail'], resolve),
		},
		{
			path: '/pages/home/setting',
			name: 'homeSetting',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/home/setting'], resolve),
		},
		{
			path: '/pages/statcenter/location',
			name: 'statcenterLocation',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/statcenter/location'], resolve),
		},
		{
			path: '/pages/notice/list',
			name: 'noticeList',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/notice/list'], resolve),
		},
		{
			path: '/pages/notice/detail',
			name: 'noticeDetail',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/notice/detail'], resolve),
		},
		{
			path: '/pages/kefu/chat', //
			name: 'kefuChat',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/kefu/chat'], resolve),
		},
		{
			path: '/pages/kefu/index', //
			name: 'kefuIndex',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/kefu/index'], resolve),
		},
		{
			path: '/pages/kefu/update', //
			name: 'kefuUpdate',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/kefu/update'], resolve),
		},
		{
			path: '/pages/kefu/setting', //
			name: 'kefuSetting',
			meta: {
				title: ''
			},
			component: resolve => require(['@/pages/kefu/setting'], resolve),
		}
	]
})



